import { motion, useScroll, useTransform } from "framer-motion";
import { useRef } from "react";

// Datele proiectelor (Aici îți vei pune tu textele reale mai târziu)
const projects = [
  {
    title: "Sistem IoT Arch",
    description: "Un dashboard complet pentru monitorizarea serverelor Linux în timp real. Construit cu Next.js și WebSockets.",
    tags: ["React", "Node.js", "Socket.io", "Linux"],
    color: "from-blue-900 to-slate-900", // Gradient de fundal
    link: "https://github.com/username/proiect1"
  },
  {
    title: "E-Commerce Ultra-Fast",
    description: "Platformă de vânzări cu randare statică și încărcare sub 0.5 secunde. Optimizare SEO agresivă.",
    tags: ["Next.js", "Stripe", "Tailwind", "Redis"],
    color: "from-purple-900 to-indigo-900",
    link: "#"
  },
  {
    title: "AI Chat Interface",
    description: "Interfață de chat modernă care integrează API-ul OpenAI, cu suport pentru streaming și istoric local.",
    tags: ["TypeScript", "OpenAI API", "Framer Motion"],
    color: "from-emerald-900 to-teal-900",
    link: "#"
  },
  {
    title: "Portofoliu V1",
    description: "Versiunea anterioară a portofoliului, axată pe minimalism și tipografie brutalistă.",
    tags: ["HTML/CSS", "Vanilla JS"],
    color: "from-orange-900 to-red-900",
    link: "#"
  }
];

// Componenta pentru un singur Card
const Card = ({ i, title, description, tags, color, link, progress, range, targetScale }: any) => {
  const container = useRef(null);
  const { scrollYProgress } = useScroll({
    target: container,
    offset: ['start end', 'start start']
  });

  // Efectul de micșorare (scale) când următorul card vine peste el
  const imageScale = useTransform(scrollYProgress, [0, 1], [2, 1]);
  const scale = useTransform(progress, range, [1, targetScale]);

  return (
    <div ref={container} className="h-screen flex items-center justify-center sticky top-0">
      <motion.div 
        style={{ scale, top: `calc(-5vh + ${i * 25}px)` }} 
        className={`flex flex-col relative -top-[25%] h-[500px] w-[1000px] rounded-3xl p-10 origin-top border border-white/10 bg-gradient-to-br ${color} shadow-2xl`}
      >
        <div className="flex h-full gap-10">
          
          {/* Partea de Text */}
          <div className="w-[40%] flex flex-col justify-between">
            <div>
              <h2 className="text-4xl font-bold mb-4 text-white">{title}</h2>
              <p className="text-gray-300 text-lg leading-relaxed">{description}</p>
            </div>
            
            <div className="flex flex-wrap gap-2 mt-4">
              {tags.map((tag: string, idx: number) => (
                <span key={idx} className="px-3 py-1 bg-white/10 rounded-full text-xs font-mono text-blue-200 border border-white/5">
                  {tag}
                </span>
              ))}
            </div>

            <a href={link} target="_blank" className="mt-6 flex items-center gap-2 text-white font-bold hover:underline group">
              Vezi Codul 
              <span className="group-hover:translate-x-1 transition-transform">→</span>
            </a>
          </div>

          {/* Partea Vizuală (Mockup) */}
          <div className="relative w-[60%] h-full rounded-2xl overflow-hidden border border-white/5 bg-black/20">
            <motion.div 
              className="w-full h-full bg-gray-800"
              style={{ scale: imageScale }}
            >
               {/* Aici ar veni imaginea proiectului real. Acum punem un placeholder abstract */}
               <div className="w-full h-full flex items-center justify-center text-gray-600 font-bold text-2xl">
                 [ Screenshot Proiect {i + 1} ]
               </div>
            </motion.div>
          </div>

        </div>
      </motion.div>
    </div>
  )
}

export default function Projects() {
  const container = useRef(null);
  const { scrollYProgress } = useScroll({
    target: container,
    offset: ['start start', 'end end']
  });

  return (
    <div ref={container} className="relative mt-[20vh] mb-[50vh]">
        <div className="mb-20 px-4 text-center">
            <h2 className="text-5xl font-bold text-white mb-4">Proiecte Recente</h2>
            <p className="text-gray-400">Arhitectură curată, cod eficient.</p>
        </div>
        
        {projects.map((project, i) => {
            // Calculăm cât de mult se micșorează fiecare card ca să se vadă cel de sub el
            const targetScale = 1 - ( (projects.length - i) * 0.05);
            return (
                <Card 
                  key={i} 
                  i={i} 
                  {...project} 
                  progress={scrollYProgress} 
                  range={[i * 0.25, 1]} 
                  targetScale={targetScale} 
                />
            );
        })}
    </div>
  );
}