"use client";

import { motion, AnimatePresence } from "framer-motion";
import { useState, useEffect } from "react";
import Projects from "./Projects"; 
import About from "./About";
import ContactTerminal from "./ContactTerminal";
import Preloader from "./Preloader";
import CustomCursor from "./CustomCursor";

export default function Home() {
  const [isLoading, setIsLoading] = useState(true);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });

  useEffect(() => {
    // Simulăm un "boot time" dacă userul dă refresh, sau poți folosi doar logica din Preloader
    const timer = setTimeout(() => {
       // Fallback în caz că preloaderul se blochează
       setIsLoading(false);
    }, 4000); 
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    const updateMouse = (e: MouseEvent) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };
    window.addEventListener("mousemove", updateMouse);
    return () => window.removeEventListener("mousemove", updateMouse);
  }, []);

  return (
    <>
      <CustomCursor />
      
      <AnimatePresence mode="wait">
        {isLoading ? (
          <Preloader key="loader" onComplete={() => setIsLoading(false)} />
        ) : (
          <motion.div 
            key="content"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.5 }}
            className="bg-black min-h-screen w-full text-white cursor-none" // cursor-none ascunde cursorul default
          >
            {/* BACKGROUND GLOBAL NOISE (Texture) */}
            <div className="fixed inset-0 z-0 opacity-[0.03] pointer-events-none bg-[url('https://grainy-gradients.vercel.app/noise.svg')]"></div>

            <div className="fixed inset-0 z-0 pointer-events-none">
                <motion.div 
                  className="absolute inset-0 bg-gradient-to-br from-blue-900/10 to-purple-900/10"
                  animate={{ x: mousePosition.x / 50, y: mousePosition.y / 50 }}
                />
                <div 
                  className="absolute w-[800px] h-[800px] bg-green-500/5 rounded-full blur-[100px]"
                  style={{ left: mousePosition.x - 400, top: mousePosition.y - 400 }}
                />
            </div>

            {/* HERO */}
            <main className="relative z-10 h-screen w-full flex flex-col items-center justify-center border-b border-white/5">
              <div className="text-center px-4">
                <motion.div 
                  initial={{ scale: 0 }} animate={{ scale: 1 }} transition={{ delay: 0.2 }}
                  className="inline-block mb-6 px-4 py-1 rounded-full border border-green-500/20 bg-green-500/5 backdrop-blur-sm"
                >
                  <span className="text-green-400 text-xs font-mono tracking-widest animate-pulse">● SYSTEM ONLINE</span>
                </motion.div>

                <h1 className="text-6xl md:text-[9rem] font-bold tracking-tighter leading-none mix-blend-difference">
                  CREATIVE
                  <span className="block text-transparent bg-clip-text bg-gradient-to-r from-gray-200 to-gray-600">
                    ENGINEER
                  </span>
                </h1>
                
                <motion.p 
                  initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ delay: 1 }}
                  className="mt-8 text-lg text-gray-500 max-w-xl mx-auto font-mono"
                >
                  [ARCH LINUX BASED ENTITY] <br/>
                  Building digital experiences from kernel to pixel.
                </motion.p>
              </div>

              <motion.div 
                  className="absolute bottom-10 flex flex-col items-center gap-2"
                  animate={{ y: [0, 10, 0] }}
                  transition={{ duration: 2, repeat: Infinity }}
              >
                  <span className="text-[10px] uppercase tracking-widest text-gray-700">Initialize Scroll</span>
              </motion.div>
            </main>

            <div className="relative z-10 space-y-32 pb-20">
              <About />
              <Projects />
              <ContactTerminal />
            </div>

            <footer className="relative z-10 py-10 border-t border-white/10 text-center">
                <p className="text-gray-600 text-sm font-mono">
                   sudo reboot system © 2025
                </p>
            </footer>

          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}